import { requestDownload } from 'utils';

const prefix = '/api/students';

export const exportFileReportBonus = ({
  courseId,
  facultyId,
  semesterId,
  studyFieldId,
  trainTypeId,
  yearEduId,
  classId,
} = {}) =>
  requestDownload({
    defaultFileName: 'fileDownload.pdf',
    prefix,
    url: '/BonusNote/ExportBonusNote',
    method: 'POST',
    data: {
      courseId,
      facultyId,
      semesterId,
      studyFieldId,
      trainTypeId,
      yearEduId,
      classId,
    },
  });



