import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Form, Col, Card, Button, message } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { exportFileReportBonus } from 'services/reportBonus';

@Form.create({ name: 'formSerchIndividual' })
class ClassDivisionPage extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = async e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      values.classId = values.class;
      exportFileReportBonus(values)
        .then(() => {})
        .catch(() => {
          message.error('Khng c sinh vin ph hp');
        });
    });
  };

  render() {
    return (
      <Fragment>
        <Card title="Bo co khen thng">
          <Row>
            <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
              <Row gutter={24}>
                <FilterPathEducate form={this.props.form} />
                <YearSemesterInput form={this.props.form} />
              </Row>
              <Row>
                <Col offset={8} span={12}>
                  <Button
                    style={{ marginTop: '30px' }}
                    htmlType="submit"
                    type="primary"
                    icon="download"
                  >
                    Sinh vin khen thng
                  </Button>
                </Col>
              </Row>
            </Form>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassDivisionPage />
  </Page>
);



